<template>
  <div>
       <van-nav-bar
      title="">
     <template #left>
    <i class="el-icon-arrow-left" @click="back"></i>
  </template>
    </van-nav-bar>
    
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh"  v-if="showflag">
      <van-icon name="add-o" /><div class="adduser" @click="add"></div>
      <div class="flush"></div>
      <div v-for="(item,index) in userlist" :key="index" @click="select(index)">
        <van-row  gutter="20">
        <van-col span="8">{{item.name}}</van-col>
        <van-col span="8">{{item.idcard}}</van-col>
        <van-col span="8">
          <span v-if="item.type==0"></span>
          <span v-else></span>
        </van-col>
      </van-row>
      </div>
      <div class="test"></div>
    </van-pull-refresh>
  </div>
</template>

<script>
export default {
  data() {
    return {
      showflag:false,
      isLoading: false,
      userlist:[{
        id:'',
        name:'',
        idcard:'330612154722541',
        type:0
      },{
        id:'',
        name:'',
        idcard:'330612154722541',
        type:1
      }]
    }
  },
  created(){
    // 
      // 
    let that = this;
    if(JSON.parse(sessionStorage.getItem('user'))){
       var id = JSON.parse(sessionStorage.getItem('user')).id;
      }

    // 
    this.$axios({
      method:'get',
      url:'/user/querypassenger',
      params:{
         id:id
      }
    }).then(function(res){
      // console.log(res);
      if(res.data.code==200){
        that.userlist=res.data.data;
        // 
        that.showflag=true;
      }
    }).catch(function (error) {
        console.log(error);
        });
        
  },
  methods:{
    onRefresh() {
      setTimeout(() => {
        this.$toast('');
        this.isLoading = false;
        this.count++;
      }, 1000);
    },
    add(){
      this.$toast('')
    },
    back(){
      this.$router.go(-1);
    },
    select(index){
      localStorage.setItem('selectuser',JSON.stringify(this.userlist[index]));
      this.$router.go(-1);
    }
  }
}
</script>

