<template>
  <div class="register_container">
    <div class="header">
    <el-row :gutter="20">
      <el-col :span="3" :offset="1"><i class="el-icon-arrow-left" @click="back"></i></el-col>
      <el-col :span="6" :offset="7"><div></div></el-col>
    </el-row>
      </div>

     

    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">

      <el-form-item label="" prop="name">
        <el-input v-model="ruleForm.username" placeholder="'_',3-15"></el-input>
      </el-form-item>
       <el-form-item  label="" prop="password">
        <el-input type="password" v-model="ruleForm.password" placeholder="6-15"></el-input>
      </el-form-item>
      <el-form-item label="" prop="confirm">
        <el-input type="password" v-model="ruleForm.confirm" placeholder=""></el-input>
      </el-form-item>


      <el-form-item label="">
        <el-select type="password" v-model="ruleForm.region" placeholder="" style="width: 100%;">
        <el-option label="" value="0"></el-option>
        </el-select>
    </el-form-item>

    <el-form-item label="" prop="idcard">
        <el-input v-model="ruleForm.idcard" placeholder=""></el-input>
      </el-form-item>
    <el-form-item label="" prop="realname">
        <el-input v-model="ruleForm.realname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model="ruleForm.phone" placeholder=""></el-input>
      </el-form-item>
      
       <el-form-item label="">
        <el-select  v-model="ruleForm.type" placeholder="" style="width: 100%;">
        <el-option label="" value="0"></el-option>
        <el-option label="" value="1"></el-option>
        </el-select>
    </el-form-item>
    <el-form-item>
      <el-checkbox v-model="ruleForm.checked"><el-button type="text" @click="open"></el-button></el-checkbox>
    </el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>

</el-form>
  </div>
</template>

<script>

export default {
  data() {
    // eslint-disable-next-line no-unused-vars
    var validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        
        ruleForm: {
          username: '',
          password: '',
          confirm:'',
          region:'0',
          idcard: '',
          realname: '',
          phone:'',
          type:'0',
          status:0,
          checked: false,
        },
        rules: {
          username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' },
            { min: 6, max: 15, message: ' 6  15 ', trigger: 'blur' }
          ],
         confirm: [
            { required: true,validator:validatePassword, trigger: 'blur' }
         ],
         phone:[
           {min: 11, max: 11, message: '', trigger: 'blur'}
         ]
        }
      };
    },
    methods: {
      open() {
        this.$dialog.alert({
        message: 'xxxxxxx',
      }).then(() => {
        // on close
      });
      },
      back(){
        this.$router.go(-1)
      },
      submitForm(formName) {
        let that = this;
        if(!this.ruleForm.checked){
          this.$notify({ type: 'warning', message: '' });
          return;
        }

        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$toast.loading({
            duration:300,
            message: '',
            forbidClick: true,
          });
            that.ruleForm.password=that.$md5(that.ruleForm.password);
            this.$axios({
              method:'post',
              url:'/user/register',
              data: this.ruleForm
            }).then(function(res){
              console.log(res)
              if(res.data.code==200){
                that.$notify({ type: 'success', message: '' });
                that.$router.push("/")
              }else if(res.data.code==302){
                 that.$notify({ type: 'warning', message: '' });
              }else{
                that.$notify({ type: 'warning', message: '' });
              }
            }).catch(function (error) {
              console.log(error);
              });
          } else {
            this.$notify({ type: 'warning', message: '' });
            console.log('error submit!!');
            return false;
          }
        });
      },
      
    }
}
</script>
