<template>
  <div class="backgroud">
    <van-nav-bar
      title=""
      left-text=""
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>

    <form action="/">
  <van-search
    v-model="value"
    show-action
    placeholder=""
    @search="onSearch"
    @cancel="onCancel"
  />

  <van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }" v-if="history.length!=0">
  
</van-divider>
  <van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }">
  
</van-divider>

<van-grid :gutter="10">
  <van-grid-item v-for="value in hotcity" :key="value" :text="value" @click="selectcity(value)"/>
</van-grid>
</form>
  </div>
</template>

<script>
export default {
data() {
    return {
      value: '',
      history:[],
      hotcity:['','','','','','','','']
    };
  },
  methods:{
    onClickLeft() {
      this.$router.go(-1);
    },
    onSearch(val) {
      this.$toast(val);
    },
    onCancel() {
      this.$toast('');
    },
    selectcity(value){
      var name = this.$route.params.city;
      sessionStorage.setItem(name,value);
      this.$router.push({
      name: 'index'
      })
    }
  }
}
</script>

